<template>
    <section>
        <el-form ref="form" :model="form.data" :rules="form.rules" label-width="80px" style="margin:20px;width:70%;min-width:600px;">

            <el-form-item label="" prop="projectId">
                <el-select v-model="form.data.projectId" :loading="init.loading" loading-text="" filterable placeholder="" @change="handleProjectChange">
                    <el-option
                            v-for="item in init.projects"
                            :label="item.uniqueName"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="branch">
                <el-select v-model="form.data.branch" :loading="init.loading" loading-text="" filterable placeholder="" :disabled="form.data.projectId === ''">
                    <el-option-group v-for="code in init.codes" :label="code.group">
                        <el-option v-for="item in code.options" :label="item" :value="item">
                        </el-option>
                    </el-option-group>
                </el-select>
            </el-form-item>

            <el-form-item label="" prop="configList">
                <el-col :span="24">
                    <el-form :inline="true">
                        <el-form-item>
                            <el-tooltip class="item" effect="dark" content="" placement="top-start">
                                <el-button @click="handleAdd" :disabled="form.data.projectId === ''"></el-button>
                            </el-tooltip>
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-table ref="config" :loading="init.loading" element-loading-text="" border :data="init.publishConfList" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="50"></el-table-column>
                    <el-table-column prop="environmentUniqueName" label=""></el-table-column>
                    <el-table-column prop="replicas" label="" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="remark" label="" show-overflow-tooltip></el-table-column>
                    <el-table-column label="">
                        <template scope="scope">
                            <el-button size="small" @click="handleEdit(scope.row)"></el-button>
                            <!--<el-button size="small" @click="handleRouteRule(scope.row)" v-if="scope.row.gray"></el-button>-->
                        </template>
                    </el-table-column>
                </el-table>
            </el-form-item>

            <!--<el-form-item label="">-->
                <!--<el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>-->
            <!--</el-form-item>-->

            <el-form-item label="">
                <el-input type="textarea" v-model="form.data.remark"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click.native="onSubmit" :loading="form.loading" :disabled="form.data.projectId === ''"></el-button>
                <el-button @click="publishHistory"></el-button>
            </el-form-item>
        </el-form>

        <!---->
        <el-dialog :visible.sync="config.dialogVisible" :title="config.title" v-model="config.visible">
            <el-form :model="config.data" label-width="100px" :rules="config.rules" ref="config">

                <el-form-item label="" prop="environmentId">
                    <el-select v-model="config.data.environmentId" :loading="config.loading" loading-text="" filterable placeholder="">
                        <el-option
                                v-for="item in init.environmentList"
                                :label="item.uniqueName"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="config.data.tomcatContextPath" auto-complete="off" placeholder="'/'" style="width:200px;"></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input-number v-model="config.data.replicas" :min="1" :max="10" :disabled="true"></el-input-number>
                    
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="config.data.ports" auto-complete="off" :disabled="true"></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input type="textarea" v-model="config.data.remark"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="config.visible = false"> </el-button>
                <el-button type="danger" @click="handleDel(config.data.id)" v-show="config.data.id != ''" :loading="config.loading"></el-button>
                <el-button type="primary" @click.native="submit" :loading="config.loading">
                    <div>{{config.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import {
        publishInit,
        publishInitBranch,
        publishInitConfig,
        publishInitEnvironment,
        publishAddConfig,
        publishEditConfig,
        publishDelConfig,
        publish
    } from '../../api/api';
    import util from '../../utils/util';

    export default {
        data() {
            return {
                userName: '',
                init: {
                    loading: true,
                    projects: [],
                    codes: [],
                    publishConfList: [],
                    environmentList: [],
                },
                form: {
                    loading: false,
                    data: {
                        id: '',
                        publishConfId: '',
                        projectId: '',
                        projectName: '',
                        environmentId: '',
                        environmentName: '',
                        branch: '',
                        remark: '',
                        status: '',
                        costTime: '',
                        createUser: '',
                        createTime: '',
                        updateUser: '',
                        updateTime: '',
                        configList: []
                    },
                    rules: {
                        projectId: [
                            {type: 'number', required: true, message: '', trigger: 'change'}
                        ],
                        branch: [
                            {type: "string", required: true, message: '', trigger: 'blur'}
                        ],
                        configList: [
                            {type: 'array', required: true, message: '', trigger: 'blur'}
                        ],
                    }
                },
                config: {
                    title: '',
                    dialogVisible: false,
                    visible: false,
                    btnText: '',
                    loading: false,
                    data: {
                        id: '',
                        projectId: '',
                        environmentId: '',
                        environmentUniqueName: '',
                        tomcatContextPath: '',
                        replicas: '',
                        ports: '',
                        remark: '',
                        createUser: '',
                        createTime: '',
                        updateUser: '',
                        updateTime: ''
                    },
                    rules: {
                        environmentId: [
                            {type: 'number', required: true, message: '', trigger: 'blur'}
                        ],
                    }
                }
            }
        },
        methods: {
            initData() {
                this.init.loading = true;
                publishInit().then((response) => {
                    if (response.type === 'success') {
                        this.init.projects = response.data.projects;
                    } else if (response.type === 'error') {
                        util.error(response.message);
                    } else {
                        util.warning(response.message);
                    }
                    this.init.loading = false;
                }, () => {//
                    util.error()
                });
                this.init.loading = false;
            },
            handleProjectChange(v) {
                this.init.loading = true;
                this.init.codes = [];
                this.form.data.branch = '';
                this.getBranch(v);
                this.getConfig(v);
                this.init.loading = false;
            },
            //branchtag
            getBranch(v) {
                let params = {projectId: v, userName: this.userName};
                publishInitBranch(params).then((response) => {
                    if (response.type === 'success') {
                        this.init.codes = response.data.codes;
                    } else if (response.type === 'error') {
                        util.error(response.message);
                    } else {
                        util.warning(response.message);
                    }
                    this.init.loading = false;
                }, () => {
                    util.error();
                });
            },
            //publish config
            getConfig(v) {
                publishInitConfig(v).then((response) => {
                    if (response.type === 'success') {
                        this.init.publishConfList = response.data.publishConfList;
                    } else if (response.type === 'error') {
                        util.error(response.message);
                    } else {
                        util.warning(response.message);
                    }
                    this.init.loading = false;
                }, () => {
                    util.error();
                });
            },
            //
            handleSelectionChange(v) {
                this.form.data.configList = v;
                if (this.form.data.configList.length > 1) {
                    util.warning("");
                } else if (this.form.data.configList.length === 1) {
                    this.form.data.publishConfId = this.form.data.configList[0].id;
                }
            },
            //
            getEnvironment() {
                publishInitEnvironment().then((response) => {
                    if (response.type === 'success') {
                        this.init.environmentList = response.data;
                    } else if (response.type === 'error') {
                        util.error(response.message);
                    } else {
                        util.warning(response.message);
                    }
                    this.config.loading = false;
                });
            },
            //
            handleAdd() {
                this.config.dialogVisible = true;
                this.config.visible = true;
                this.config.title = '';
                this.config.loading = true;
                //
                this.getEnvironment();
                this.config.data.id = '';
                this.config.data.projectId = this.form.data.projectId;
                this.config.data.environmentId = '';
                this.config.data.environmentUniqueName = '';
                this.config.data.tomcatContextPath = '';
                this.config.data.replicas = '';
                this.config.data.ports = '8080';
                this.config.data.remark = '';
                this.config.data.createUser = this.userName;
                this.config.data.createTime = '';
                this.config.data.updateUser = this.userName;
                this.config.data.updateTime = '';
            },
            //
            handleEdit(row) {
                this.config.dialogVisible = true;
                this.config.visible = true;
                this.config.title = '';
                this.config.loading = true;
                this.getEnvironment();
                this.config.data.id = row.id;
                this.config.data.projectId = row.projectId;
                this.config.data.environmentId = row.environmentId;
                this.config.data.environmentUniqueName = row.environmentUniqueName;
                this.config.data.tomcatContextPath = row.tomcatContextPath;
                this.config.data.replicas = row.replicas;
                this.config.data.ports = row.ports;
                this.config.data.remark = row.remark;
                this.config.data.createUser = row.createUser;
                this.config.data.createTime = row.createTime;
                this.config.data.updateUser = this.userName;
                this.config.data.updateTime = row.updateTime;
            },
            //
            handleDel(id) {
                this.$confirm('?', '', {type: 'warning'}).then(() => {
                    this.config.loading = true;
                    publishDelConfig(id).then((response) => {
                        if (response.type === "success") {
                            util.success(response.message);
                        } else if (response.type === "error") {
                            util.error(response.message);
                        } else {
                            util.warning(response.message);
                        }
                        this.getConfig(this.config.data.projectId);
                    }, () => {
                        util.error();
                    });
                    this.config.loading = false;
                    this.config.visible = false;
                });
            },
            submit() {
                this.$refs.config.validate((valid) => {
                    if (valid) {
                        this.$confirm('?', '', {}).then(() => {
                            this.config.loading = true;
                            this.config.btnText = '';
                            var formData = this.config.data;
                            let param = Object.assign({}, formData);
                            if (this.config.data.id) {
                                publishEditConfig(param).then((response) => {
                                    if (response.type === "success") {
                                        util.success(response.message);
                                    } else if (response.type === "error") {
                                        util.error(response.message);
                                    } else {
                                        util.warning(response.message);
                                    }
                                    this.getConfig(this.config.data.projectId);
                                }, () => {
                                    util.error();
                                });
                            } else {
                                publishAddConfig(param).then((response) => {
                                    if (response.type === "success") {
                                        util.success(response.message);
                                    } else if (response.type === "error") {
                                        util.error(response.message);
                                    } else {
                                        util.warning(response.message);
                                    }
                                    this.getConfig(this.config.data.projectId);
                                }, () => {
                                    util.error();
                                });
                            }
                            this.config.loading = false;
                            this.config.btnText = '';
                            this.config.visible = false;
                            this.config.dialogVisible = false;
                        });
                    }
                });
            },
            //
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            },
            //
            onSubmit() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.form.data.configList.length > 1) {
                            util.warning("")
                            return;
                        }

                        this.form.data.createUser = this.userName;
                        this.form.data.updateUser = this.userName;
                        publish(this.form.data).then((response) => {
                            if (response.type === "success") {
                                // util.success(response.message);
                            } else if (response.type === "error") {
                                util.error(response.message);
                            } else {
                                util.warning(response.message);
                            }
                            this.$router.push({name: '', params: {}});
                        }, () => {
                            util.error();
                        });
                    } else {
                        return false;
                    }
                });
            },
            publishHistory() {
                this.$router.push({name: '', params: {}});
            }
        },
            mounted() {
            this.initData();
            this.getUserName();
        }
    }

</script>


