<template>
    <section>
        <!---->
        <el-row :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-col :span="12">
                <el-form :inline="true" :model="filters" class="grid-content bg-purple">
                    <el-form-item>
                        <el-input v-model="filters.keyword" placeholder=""></el-input>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="1" :offset="11">
                <el-form class="grid-content bg-purple-light">
                    <el-form-item>
                        <el-button type="primary" @click="handleAdd"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <!---->
        <el-table :data="environmentList" highlight-current-row v-loading="table.loading" element-loading-text="" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="uniqueName" label="" min-width="100" sortable>
            </el-table-column>
            <el-table-column prop="ip" label="ip" min-width="100" sortable>
            </el-table-column>
            <el-table-column prop="userName" label="" sortable>
            </el-table-column>
            <el-table-column prop="status" label="" sortable>
                <template scope="scope">
                    <el-tag v-if="scope.row.status === 1" type="success"></el-tag>
                    <el-tag v-else-if="scope.row.status === -1" type="danger"></el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="remark" label="" min-width="100" sortable>
            </el-table-column>
            <el-table-column prop="updateUser" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="updateTime" label="" width="180" sortable>
            </el-table-column>
            <el-table-column label="" min-width="150">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <!---->
            <el-pagination
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    :current-page="table.page" :page-sizes="[10, 25, 50]" :page-size="table.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="environmentList.length" style="float:right">
            </el-pagination>
        </el-col>

        <!---->
        <el-dialog :title="form.title" v-model="form.visible" :close-on-click-modal="false">
            <el-form :model="form.data " label-width="100px" :rules="form.rules" ref="form">
                <el-form-item label="" prop="uniqueName">
                    <el-input v-model="form.data.uniqueName" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="ip" prop="ip">
                    <el-input v-model="form.data.ip" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="userName">
                    <el-input v-model="form.data.userName" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input type=password v-model="form.data.password" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <!--<el-input v-model="form.data.status" auto-complete="off" :disabled="true"></el-input>-->
                    <template scope="scope">
                        <el-tag v-if="form.data.status === 1" type="success"></el-tag>
                        <el-tag v-else-if="form.data.status === -1" type="danger"></el-tag>
                        <el-tag v-else type="warning"></el-tag>
                    </template>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="form.data.remark"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="form.visible = false"> </el-button>
                <!--<el-button type="danger" @click="handleDel(form.data)" v-show="form.data.id != ''"></el-button>-->
                <el-button type="primary" @click.native="submit" :loading="form.loading">
                    <div>{{form.btnText}}</div>
                </el-button>
            </div>
        </el-dialog>

    </section>
</template>

<script>
    import {getEnvironmentList, addEnvironment, editEnvironment, removeEnvironment} from '../../api/api'

    export default {
        data() {
            return {
                filters: {
                    keyword: ''
                },
                sels: [],//
                userName: '',
                table: {
                    loading: false,
                    page: 1,
                    size: 10,
                    data: []
                },
                form: {
                    title: '',
                    visible: false,//
                    loading: false,
                    btnText: '',
                    data: {
                        id: '',
                        uniqueName: '',
                        ip: '',
                        userName: '',
                        password: '',
                        status: '',
                        remark: '',
                        createTime: '',
                        createUser: '',
                        updateTime: '',
                        updateUser: ''
                    },
                    rules: {
                        uniqueName: [
                            {required: true, message: '', trigger: 'blur'}
                        ],
                        ip: [
                            {required: true, message: 'ip', trigger: 'blur'}
                        ],
                        userName: [
                            {required: true, message: '', trigger: 'blur'}
                        ],
                        password: [
                            {required: true, message: '', trigger: 'blur'}
                        ],
                    }
                }
            };
        },
        computed: {
            environmentList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size, keyword = self.filters.keyword;
                var v = data.filter(function (p) {
                    return (p.uniqueName && p.uniqueName.indexOf(keyword) !== -1)
                        || (p.ip && p.ip.indexOf(keyword) !== -1)
                        || (p.userName && p.userName.indexOf(keyword) !== -1)
                        || (p.updateUser && p.updateUser.indexOf(keyword) !== -1)
                });
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            getEnvironment() {
                this.table.loading = true;
                getEnvironmentList().then((data) => {
                    this.table.data = data;
                    this.table.loading = false;
                })
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            handleSizeChange(val) {
                this.table.size = val;
            },
            handleAdd: function () {
                this.form.visible = true;
                this.form.title = '';
                this.form.data.id = '';
                this.form.data.uniqueName = '';
                this.form.data.ip = '';
                this.form.data.userName = '';
                this.form.data.password = '';
                this.form.data.status = '';
                this.form.data.remark = '';
                this.form.data.createTime = '';
                this.form.data.createUser = this.userName;
                this.form.data.updateTime = '';
                this.form.data.updateUser = this.userName;
            },
            handleEdit: function (index, row) {
                this.form.visible = true;
                this.form.title = '';
                this.form.data.id = row.id;
                this.form.data.uniqueName = row.uniqueName;
                this.form.data.ip = row.ip;
                this.form.data.userName = row.userName;
                this.form.data.password = row.password;
                this.form.data.status = row.status;
                this.form.data.remark = row.remark;
                this.form.data.createTime = row.createTime;
                this.form.data.createUser = row.createUser;
                this.form.data.updateTime = row.updateTime;
                this.form.data.updateUser = this.userName;
            },
            submit: function () {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$confirm('?', '', {}).then(() => {
                            this.form.loading = true;
                            this.form.btnText = '';
                            var formData = this.form.data;
                            let param = Object.assign({}, formData);
                            if (formData.id) {
                                editEnvironment(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getEnvironment();
                                });
                            } else {
                                addEnvironment(param).then((data) => {
                                    let {message, type} = data;
                                    this.$message({
                                        showClose: true,
                                        message: message,
                                        type: type
                                    });
                                    this.getEnvironment();
                                });
                            }
                            this.form.loading = false;
                            this.form.btnText = '';
                            this.form.visible = false;
                        });
                    }
                });
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.table.loading = true;
                    //NProgress.start();
                    let para = {id: row.id};
                    removeEnvironment(para).then((res) => {
                        this.table.loading = false;
                        //NProgress.done();
                        let {message, type} = res;
                        this.$message({
                            showClose: true,
                            message: message,
                            type: type
                        });
                        this.getEnvironment();
                    });
                }).catch(() => {

                });
            },
            //
            getUserName: function () {
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.userName = user.account;
                }
            },
        },
        mounted() {
            this.getEnvironment();
            this.getUserName();
        }
    }

</script>


