import Vue from "vue";
// import VueResource from "vue-resource";
import {MessageBox, Notification} from "element-ui";

// Vue.use(VueResource);

export default {
    success: function (msg) {
        Notification.success({
            title: '',
            message: msg
        });
    },
    warning: function (msg) {
        Notification.warning({
            title: '',
            message: msg,
            duration: 30000
        });
    },
    error: function (msg) {
        Notification.error({
            title: '',
            message: msg,
            duration: 30000
        })
    },
};


