import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import User from './views/user/User.vue'
import PublishHistory from './views/publish/PublishHistory.vue'
import Publish from './views/publish/Publish.vue'
import Project from './views/manage/Project.vue'
import Environment from './views/manage/Environment.vue'
import Token from './views/manage/Token.vue'
import echarts from './views/statistics/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            // {path: '/main', component: Main, name: '', hidden: true},
            {path: '/publish', component: Publish, name: ''},
            {path: '/history', component: PublishHistory, name: ''},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            {path: '/project', component: Project, name: ''},
            {path: '/environment', component: Environment, name: ''},
            {path: '/token', component: Token, name: 'token'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            {path: '/user', component: User, name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            {path: '/echarts', component: echarts, name: 'echarts'}
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: {path: '/404'}
    }
];

export default routes;


